@import-normalize;

:root {
	--gray: #efebe9;
	--color-text: #e57373;
}

.App {
	display: grid;
	margin: 0 auto;
	width: 1200px;
	grid-template-areas:
		'h h'
		'n c';
	grid-template-rows: 60px 1fr;
	grid-template-columns: 2fr 10fr;
	grid-gap: 10px;
	font-family: 'Karla', sans-serif;
}

.App__Content {
	grid-area: c;
	background-color: #efebe9;
	border-radius: 20px;
	padding: 20px;
}
